*{
    margin: 0;
    padding: 0;
}
.quote-box{
    width: 95%;
    max-width: 650px;
    min-height: 30vh;
    background-color: #f2f2f2;
    border-radius: 14px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-image: url( './Assets/quote.svg' );
    background-repeat: no-repeat;
    background-position: top left;
    background-size: 20vmax;
    background-blend-mode: soft-light;
}
.quoteText{
    width: 100%;
    display: flex;
    justify-content: center;
    position: relative;
    height: 75%;
    align-items: center;
}
.quoteText span{
    padding: 30px;
    font-weight: 400;
    font-size:1.35em;
}
.quoteAuthor{
    height: 10%;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    font-size:1em;
    align-items: center;
}
.quoteAuthor span{
    padding-right: 32px;
    font-weight: 200;
}
.quoteInfo{
    display: flex;
    height: 15%;
    padding: 20px;
    justify-content: space-around;
}   
.button{
    border: 1px solid #262626;
    background-color: #f2f2f2;
    padding: .5rem 2rem;
    transition: 0.4s ;
    font-weight: 200;
    color: #262626;
    text-transform: lowercase;
    border-radius: 10px;
    font-size:.8em;
    text-decoration: none;
}
a{
    color: #262626;
    text-decoration: none;
}
.fa-brands{
    font-size:2em;
    color: #262626;
    transition: 0.4s;
}
.fa-brands:hover{
    color: black;
}
.button:hover{
    cursor: pointer;
    background-color: #262626;
    color: #f2f2f2;
}
footer{
    margin-top: 5px;
    color: #f2f2f2;
}
@media only screen and (max-width: 300px) {
    .quoteInfo {
        flex-direction: column;
    }
    .button {
        margin-bottom: 8px;
        width: 100%;
    }
  }